<template>
  <div class="fraction-game">
    <div class="game-header">
      <h2>üî¢ ÂàÜÊï∞Ê∏∏Êàè</h2>
      <div class="game-stats">
        <span class="score">ÂæóÂàÜ: {{ score }}</span>
        <span class="time">Êó∂Èó¥: {{ timeLeft }}s</span>
        <span class="level">Á≠âÁ∫ß: {{ level }}</span>
      </div>
    </div>

    <div v-if="!gameStarted" class="game-start">
      <div class="start-content">
        <h3>ÂáÜÂ§áÂºÄÂßãÂàÜÊï∞ÊåëÊàòÔºÅ</h3>
        <p>ÈÄöËøáÂèØËßÜÂåñÊñπÂºèÂ≠¶‰π†ÂàÜÊï∞ÔºåÁêÜËß£ÂàÜÂ≠êÂàÜÊØçÁöÑÊ¶ÇÂøµ</p>
        <div class="difficulty-select">
          <label>ÈÄâÊã©ÈöæÂ∫¶:</label>
          <select v-model="selectedDifficulty">
            <option value="easy">ÁÆÄÂçï (Âü∫Êú¨ÂàÜÊï∞)</option>
            <option value="medium">‰∏≠Á≠â (ÂàÜÊï∞ÊØîËæÉ)</option>
            <option value="hard">Âõ∞Èöæ (ÂàÜÊï∞ËøêÁÆó)</option>
          </select>
        </div>
        <button class="btn btn-primary" @click="startGame">ÂºÄÂßãÊ∏∏Êàè</button>
      </div>
    </div>

    <div v-else-if="!gameOver" class="game-play">
      <div class="question-container">
        <div class="question">
          <div v-if="gameType === 'visual'" class="visual-fraction">
            <div class="fraction-display">
              <div class="numerator">{{ fraction1.numerator }}</div>
              <div class="fraction-line">‚îÄ</div>
              <div class="denominator">{{ fraction1.denominator }}</div>
            </div>
            <div class="operator">{{ operator }}</div>
            <div class="fraction-display">
              <div class="numerator">{{ fraction2.numerator }}</div>
              <div class="fraction-line">‚îÄ</div>
              <div class="denominator">{{ fraction2.denominator }}</div>
            </div>
            <div class="equals">=</div>
            <div class="answer-input">
              <input 
                v-model="userAnswer" 
                type="text" 
                placeholder="ËæìÂÖ•Á≠îÊ°à"
                @keyup.enter="checkAnswer"
                ref="answerInput"
                autofocus
              />
            </div>
          </div>
          
          <div v-else-if="gameType === 'compare'" class="compare-fraction">
            <div class="fraction-pair">
              <div class="fraction-display">
                <div class="numerator">{{ fraction1.numerator }}</div>
                <div class="fraction-line">‚îÄ</div>
                <div class="denominator">{{ fraction1.denominator }}</div>
              </div>
              <div class="compare-operator">
                <select v-model="userComparison">
                  <option value="">ÈÄâÊã©</option>
                  <option value=">">&gt;</option>
                  <option value="=">=</option>
                  <option value="<">&lt;</option>
                </select>
              </div>
              <div class="fraction-display">
                <div class="numerator">{{ fraction2.numerator }}</div>
                <div class="fraction-line">‚îÄ</div>
                <div class="denominator">{{ fraction2.denominator }}</div>
              </div>
            </div>
          </div>
          
          <div v-else class="text-question">
            <p>{{ questionText }}</p>
            <div class="answer-input">
              <input 
                v-model="userAnswer" 
                type="text" 
                placeholder="ËæìÂÖ•Á≠îÊ°à (Â¶Ç: 3/4)"
                @keyup.enter="checkAnswer"
                ref="answerInput"
                autofocus
              />
            </div>
          </div>
        </div>
        
        <button class="btn btn-primary" @click="checkAnswer">Á°ÆËÆ§</button>
      </div>

      <div class="progress-bar">
        <div class="progress" :style="{ width: progressPercent + '%' }"></div>
      </div>

      <div class="feedback" v-if="showFeedback">
        <div :class="['feedback-message', isCorrect ? 'correct' : 'incorrect']">
          {{ feedbackMessage }}
        </div>
      </div>

      <div class="hint" v-if="showHint">
        <p>üí° ÊèêÁ§∫: {{ hintMessage }}</p>
      </div>
    </div>

    <div v-else class="game-over">
      <div class="game-over-content">
        <h3>Ê∏∏ÊàèÁªìÊùüÔºÅ</h3>
        <div class="final-stats">
          <p>ÊúÄÁªàÂæóÂàÜ: <span class="final-score">{{ score }}</span></p>
          <p>Ê≠£Á°ÆÈ¢òÁõÆ: {{ correctAnswers }}/{{ totalQuestions }}</p>
          <p>Áî®Êó∂: {{ totalTime }}Áßí</p>
        </div>
        <div class="game-actions">
          <button class="btn btn-primary" @click="restartGame">ÂÜçÁé©‰∏ÄÊ¨°</button>
          <button class="btn btn-secondary" @click="goBack">ËøîÂõûÊ∏∏ÊàèÂàóË°®</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import { gameStorage } from '@/services/gameStorage'
import { achievementService } from '@/services/achievementService'

const router = useRouter()

// Ê∏∏ÊàèÁä∂ÊÄÅ
const gameStarted = ref(false)
const gameOver = ref(false)
const selectedDifficulty = ref('medium')
const score = ref(0)
const timeLeft = ref(60)
const level = ref(1)
const correctAnswers = ref(0)
const totalQuestions = ref(0)
const totalTime = ref(0)

// Ê∏∏ÊàèÈ¢òÁõÆ
const gameType = ref('visual')
const fraction1 = ref({ numerator: 0, denominator: 1 })
const fraction2 = ref({ numerator: 0, denominator: 1 })
const operator = ref('+')
const questionText = ref('')
const userAnswer = ref('')
const userComparison = ref('')
const showFeedback = ref(false)
const isCorrect = ref(false)
const feedbackMessage = ref('')
const showHint = ref(false)
const hintMessage = ref('')

// ÂÆöÊó∂Âô®
let gameTimer: NodeJS.Timeout | null = null
let feedbackTimer: NodeJS.Timeout | null = null

// ÈöæÂ∫¶ÈÖçÁΩÆ
const difficultyConfig = {
  easy: { time: 60, maxDenominator: 6 },
  medium: { time: 60, maxDenominator: 10 },
  hard: { time: 60, maxDenominator: 12 }
}

// ËÆ°ÁÆóËøõÂ∫¶ÁôæÂàÜÊØî
const progressPercent = computed(() => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  return ((config.time - timeLeft.value) / config.time) * 100
})

// ÁîüÊàêÊñ∞È¢òÁõÆ
const generateQuestion = () => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  
  if (selectedDifficulty.value === 'easy') {
    // ÁÆÄÂçïÊ®°ÂºèÔºöÂü∫Êú¨ÂàÜÊï∞ËØÜÂà´
    gameType.value = 'visual'
    fraction1.value = generateSimpleFraction(config.maxDenominator)
    fraction2.value = generateSimpleFraction(config.maxDenominator)
    operator.value = '+'
    hintMessage.value = 'ËßÇÂØüÂàÜÂ≠êÂíåÂàÜÊØçÔºåÁêÜËß£ÂàÜÊï∞ÁöÑÂê´‰πâ'
  } else if (selectedDifficulty.value === 'medium') {
    // ‰∏≠Á≠âÊ®°ÂºèÔºöÂàÜÊï∞ÊØîËæÉ
    gameType.value = 'compare'
    fraction1.value = generateSimpleFraction(config.maxDenominator)
    fraction2.value = generateSimpleFraction(config.maxDenominator)
    hintMessage.value = 'ÊØîËæÉ‰∏§‰∏™ÂàÜÊï∞ÁöÑÂ§ßÂ∞èÔºåÂèØ‰ª•ËΩ¨Êç¢‰∏∫Â∞èÊï∞'
  } else {
    // Âõ∞ÈöæÊ®°ÂºèÔºöÂàÜÊï∞ËøêÁÆó
    gameType.value = 'text'
    const operators = ['+', '-', '√ó', '√∑']
    operator.value = operators[Math.floor(Math.random() * operators.length)]
    
    if (operator.value === '√∑') {
      // Èô§Ê≥ïÔºöÁ°Æ‰øùËÉΩÊï¥Èô§
      const result = generateSimpleFraction(config.maxDenominator)
      const divisor = generateSimpleFraction(config.maxDenominator)
      fraction1.value = {
        numerator: result.numerator * divisor.denominator,
        denominator: result.denominator * divisor.numerator
      }
      fraction2.value = divisor
      questionText.value = `ËÆ°ÁÆó ${fraction1.value.numerator}/${fraction1.value.denominator} √∑ ${fraction2.value.numerator}/${fraction2.value.denominator}`
    } else {
      fraction1.value = generateSimpleFraction(config.maxDenominator)
      fraction2.value = generateSimpleFraction(config.maxDenominator)
      questionText.value = `ËÆ°ÁÆó ${fraction1.value.numerator}/${fraction1.value.denominator} ${operator.value} ${fraction2.value.numerator}/${fraction2.value.denominator}`
    }
    
    hintMessage.value = 'ÂÖàÈÄöÂàÜÔºåÂÜçËøõË°åËøêÁÆóÔºåÊúÄÂêéÁ∫¶ÂàÜ'
  }
  
  userAnswer.value = ''
  userComparison.value = ''
  totalQuestions.value++
  
  // ÊòæÁ§∫ÊèêÁ§∫
  showHint.value = true
  setTimeout(() => {
    showHint.value = false
  }, 4000)
}

// ÁîüÊàêÁÆÄÂçïÂàÜÊï∞
const generateSimpleFraction = (maxDenominator: number) => {
  const denominator = Math.floor(Math.random() * (maxDenominator - 1)) + 2
  const numerator = Math.floor(Math.random() * denominator) + 1
  return { numerator, denominator }
}

// Ê£ÄÊü•Á≠îÊ°à
const checkAnswer = () => {
  let isAnswerCorrect = false
  
  if (gameType.value === 'compare') {
    if (!userComparison.value) return
    
    const value1 = fraction1.value.numerator / fraction1.value.denominator
    const value2 = fraction2.value.numerator / fraction2.value.denominator
    
    if (userComparison.value === '>' && value1 > value2) isAnswerCorrect = true
    else if (userComparison.value === '=' && Math.abs(value1 - value2) < 0.001) isAnswerCorrect = true
    else if (userComparison.value === '<' && value1 < value2) isAnswerCorrect = true
  } else {
    // Á°Æ‰øù userAnswer ÊòØÊúâÊïàÁöÑÂ≠óÁ¨¶‰∏≤‰∏î‰∏ç‰∏∫Á©∫
    const answerStr = userAnswer.value?.toString()?.trim()
    if (!answerStr) return
    
    // Ëß£ÊûêÁî®Êà∑ËæìÂÖ•ÁöÑÂàÜÊï∞
    const fractionMatch = answerStr.match(/^(\d+)\/(\d+)$/)
    if (fractionMatch) {
      const userNumerator = parseInt(fractionMatch[1])
      const userDenominator = parseInt(fractionMatch[2])
      
      if (gameType.value === 'visual') {
        // ÁÆÄÂçïÊ®°ÂºèÔºöÊ£ÄÊü•ÂàÜÊï∞ÊòØÂê¶Ê≠£Á°Æ
        isAnswerCorrect = userNumerator === fraction1.value.numerator && 
                         userDenominator === fraction1.value.denominator
      } else {
        // Âõ∞ÈöæÊ®°ÂºèÔºöÊ£ÄÊü•ËøêÁÆóÁªìÊûú
        const result = calculateFractionOperation()
        isAnswerCorrect = userNumerator === result.numerator && 
                         userDenominator === result.denominator
      }
    }
  }
  
  if (isAnswerCorrect) {
    isCorrect.value = true
    feedbackMessage.value = '‚úÖ Ê≠£Á°ÆÔºÅ'
    score.value += 10 * level.value
    correctAnswers.value++
    
    // ÊØèÁ≠îÂØπ5È¢òÂçá‰∏ÄÁ∫ß
    if (correctAnswers.value % 5 === 0) {
      level.value++
      feedbackMessage.value += ` ÂçáÁ∫ßÂà∞ ${level.value} Á∫ßÔºÅ`
    }
  } else {
    isCorrect.value = false
    if (gameType.value === 'compare') {
      const value1 = fraction1.value.numerator / fraction1.value.denominator
      const value2 = fraction2.value.numerator / fraction2.value.denominator
      let correctAnswer = ''
      if (value1 > value2) correctAnswer = '>'
      else if (Math.abs(value1 - value2) < 0.001) correctAnswer = '='
      else correctAnswer = '<'
      feedbackMessage.value = `‚ùå ÈîôËØØÔºÅÊ≠£Á°ÆÁ≠îÊ°àÊòØ ${correctAnswer}`
    } else {
      feedbackMessage.value = '‚ùå ÈîôËØØÔºÅËØ∑Ê£ÄÊü•‰Ω†ÁöÑÁ≠îÊ°à'
    }
    score.value = Math.max(0, score.value - 5)
  }
  
  showFeedback.value = true
  
  // Ê∏ÖÈô§ÂèçÈ¶àÊèêÁ§∫
  if (feedbackTimer) clearTimeout(feedbackTimer)
  feedbackTimer = setTimeout(() => {
    showFeedback.value = false
    generateQuestion()
  }, 2000)
}

// ËÆ°ÁÆóÂàÜÊï∞ËøêÁÆó
const calculateFractionOperation = () => {
  const num1 = fraction1.value.numerator / fraction1.value.denominator
  const num2 = fraction2.value.numerator / fraction2.value.denominator
  let result = 0
  
  switch (operator.value) {
    case '+': result = num1 + num2; break
    case '-': result = num1 - num2; break
    case '√ó': result = num1 * num2; break
    case '√∑': result = num1 / num2; break
  }
  
  // ËΩ¨Êç¢‰∏∫ÂàÜÊï∞ÂΩ¢ÂºèÔºàÁÆÄÂåñÂ§ÑÁêÜÔºâ
  const denominator = 100
  const numerator = Math.round(result * denominator)
  return { numerator, denominator }
}

// ÂºÄÂßãÊ∏∏Êàè
const startGame = () => {
  gameStarted.value = true
  gameOver.value = false
  score.value = 0
  level.value = 1
  correctAnswers.value = 0
  totalQuestions.value = 0
  totalTime.value = 0
  
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  timeLeft.value = config.time
  
  generateQuestion()
  startTimer()
}

// ÂºÄÂßãËÆ°Êó∂Âô®
const startTimer = () => {
  gameTimer = setInterval(() => {
    timeLeft.value--
    totalTime.value++
    
    if (timeLeft.value <= 0) {
      endGame()
    }
  }, 1000)
}

// ÁªìÊùüÊ∏∏Êàè
const endGame = () => {
  if (gameTimer) {
    clearInterval(gameTimer)
    gameTimer = null
  }
  gameOver.value = true
  
  // ‰øùÂ≠òÊ∏∏ÊàèËÆ∞ÂΩï
  gameStorage.addGameRecord({
    gameType: 'fractions',
    gameName: 'ÂàÜÊï∞Ê∏∏Êàè',
    score: score.value,
    level: level.value,
    correctAnswers: correctAnswers.value,
    totalQuestions: totalQuestions.value,
    totalTime: totalTime.value,
    difficulty: selectedDifficulty.value
  })

  // Ê£ÄÊü•ÊàêÂ∞±
  achievementService.checkAchievements()
}

// ÈáçÊñ∞ÂºÄÂßãÊ∏∏Êàè
const restartGame = () => {
  gameStarted.value = false
  gameOver.value = false
  userAnswer.value = ''
  userComparison.value = ''
  showFeedback.value = false
  showHint.value = false
}

// ËøîÂõûÊ∏∏ÊàèÂàóË°®
const goBack = () => {
  router.push('/games')
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ËÅöÁÑ¶ËæìÂÖ•Ê°Ü
onMounted(() => {
  if (gameStarted.value && !gameOver.value) {
    setTimeout(() => {
      const input = document.querySelector('input') as HTMLInputElement
      if (input) input.focus()
    }, 100)
  }
})

// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜÂÆöÊó∂Âô®
onUnmounted(() => {
  if (gameTimer) clearInterval(gameTimer)
  if (feedbackTimer) clearTimeout(feedbackTimer)
})
</script>

